body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.img-wrapper {
  border: 1px solid black;
  margin: 1% 0 1% 1%;
  min-width: 100px;
  min-height: 100px;
  max-width: 100px;
  max-height: 100px;
}

.img {
  max-width: 100%;
  max-height: 100%;
  object-fit: contain;
}

.item-text-wrapper {
  margin: 0 2%;
  flex-grow: 1;
  
}



.event-list-container {
  border: 2px solid red;
  max-height: 800px;
  overflow-y: scroll
}

.event-list-item-wrapper {
  border: 2px solid black;
  box-sizing: border-box;
  display: flex;
}




.UserPersonalData-container{
 border: 2px solid black;
}

.UserEvents-container {
  border: 2px solid black;
}
.UserPersonalData-main-profil {
  display: flex;
}

.UserPersonalData-name {
  padding: 20px 30px;
}

.UserPersonalData-surname {
  padding: 20px 30px;
}

.UserPersonalData-description {
  padding-top: 20px;
}

.EventDescription-description{
  display: flex;
  
  padding: 10px 10px
}
.EventDescription-description--buttonarea{
  display: flex;
  justify-content: flex-end;
}
.EventDescription-description--button{
  height:50px;
  width: 50px;
  border-radius: 15px;
  font-size: 16px;
}
.EventDescription-description--text{

  padding: 10px;

}



